<template>
	<div :class="['intro', isShifted]">
		<div class="intro-row">
			<div class="intro-column left">
				<p>  <b>Light and Shadow</b>   ,       .   ,     ,   ,   .   ,   WOW  . </p>
			</div>
			<div class="intro-column right">
				<div class="intro-camera"></div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'intro',
		props: [
			'shifted'
		],
		computed: {
			isShifted: function(){
				if(this.shifted)
					return 'shifted';
				else
					return '';
			}
		}
	};
</script>


