<template>
	<div class="slider">
		<div class="slider-item">
			<aside>
				<h3> </h3>
				<p>     .  ,         ,   ,     . </p>
			</aside>
			<aside>
				<h3> </h3>
				<div class="slider-aside-row">
					<h4> </h4>
					<h4> </h4>
				</div>
				<div class="slider-aside-row">
					<h4>Landing page</h4>
					<h4> </h4>
				</div>
			</aside>
		</div>
		<div class="slider-item">
			<aside>
				<h3> </h3>
				<p>  ,       .          .    -     -  .</p>
			</aside>
			<aside>
				<h3> </h3>
				<div class="slider-aside-row">
					<h4> </h4>
					<h4></h4>
				</div>
				<div class="slider-aside-row">
					<h4> |   .</h4>
					<h4></h4>
				</div>
			</aside>
		</div>
	</div>
</template>

<script>
	import $ from 'jquery';
	import 'slick-carousel';
	import '../../../node_modules/slick-carousel/slick/slick.css';

	export default {
		name: 'slider',
		mounted: function(){
			$('.slider').slick({
				dots: true,
				autoplay: true,
				autoplaySpeed: 3000,
				prevArrow: '<button class="slick-prev slick-arrow" aria-label="Previous" type="button"><span><i class="fas fa-caret-left"></i></span></button>',
				nextArrow: '<button class="slick-next slick-arrow" aria-label="Next" type="button"><span><i class="fas fa-caret-right"></i></span></button>'
			});
		}
	};
</script>


