<script>
import Navbar from '../components/Navbar.vue';
import FooterBlock from '../components/Footer.vue';
import $ from 'jquery';

	export default {
		name: 'example',
		data: function(){
			return {
				examples: [
					'dr-grape',
					'perlina',
					'pinsel',
					'zakarpatsky'
				],
				titles: [
					'Dr. Grape | Pinsel Studio -  / ',
					'  | Pinsel Studio -  ',
					'Pinsel Studio -  -',
					' | Pinsel Studio -  '
				],
				drGrape: false,
				perlina: false,
				pinsel: false,
				zakarpatsky: false
			};
		},
		components: {
			Navbar,
			FooterBlock
		},
		mounted: function(){
			$(window).unbind('mousewheel DOMMouseScroll MozMousePixelScroll');
			var id = this.$route.params.id;
			var index = this.examples.indexOf(this.$route.params.id);
			if(index == -1){
				this.$emit('redirect404');
			} else {
				document.title = this.titles[index];
				var bdy = document.getElementById('bdy');
				if(id == 'dr-grape'){
					this.drGrape = true;
        	bdy.className = "dr-grape-body";
				}
				if(id == 'perlina'){
					this.perlina = true;
					bdy.className = "perlina-body";
				}
				if(id == 'pinsel'){
					this.pinsel = true;
					bdy.className = "pinsel-body";
				}
				if(id == 'zakarpatsky'){
					this.zakarpatsky = true;
					bdy.className = "zakarpatsky-body";
				}
			}
		}
	};
</script>


